mplate-rows: auto 1fr auto;\n    min-height: 100vh;\n    max-width: 1920px;\n    margin: 0 auto;\n  }\n\n  .icons {\n    width: 2em;\n  }\n\n  /* HEADER & FOOTER */\n\n  header,\n  footer {\n    grid-column: 1/3;\n    background: var(--linear-gradient);\n  }\n\n  header {\n    grid-row: 1/2;\n    padding: 2em;\n  }\n\n  .logo {\n    display: flex;\n    align-items: center;\n    gap: 8px;\n  }\n\n  .logo-text, \n  footer {\n    font-family: 'Neuropolitical';\n    color: var(--off-white);\n    text-shadow: var(--text-shadow);\n  }\n\n  footer { \n    grid-row: 3/4;\n    text-align: center;\n    font-size: 1.25rem;\n    padding: 0.5em;\n    color: var(--off-white);\n  }\n\n  /* ASIDE */\n\n  aside {\n    display: flex;\n    flex-direction: column;\n    gap: 1em;\n    padding: 2em;\n    background-color: var(--light-gray)\n  }\n\n  .my-projects {\n    display: grid;\n    grid-template-rows:  repeat(3, 1fr);\n    gap: 1em;\n    padding-right: 2em;\n    max-width: 600px;\n  }\n\n  .project-card {\n    display: flex;\n    justify-content: space-between;\n  }\n\n  .task-counters {\n    align-content: center;\n    \n  }\n\n  .counter {\n    font-size: 1.5rem;\n    font-weight: 900;\n  }\n\n  /* MAIN */\n\n  main {\n    padding: 2em 10em 2em 5em;\n    background-color: var(--light-gray);\n  }\n\n  /* Active Project */\n\n  .active-project {\n    padding: 1em;\n    display: flex;\n    justify-content: space-between;\n    border-bottom: 2px solid var(--medium-gray);\n    max-width: 1200px;\n  }\n\n  .project-header {\n    white-space: nowrap; \n    overflow: hidden; \n    text-overflow: ellipsis; \n    max-width: 350px;\n  }\n\n  .project-options {\n    display: flex;\n    gap: 1em;\n  }\n\n  /* Task Cards */\n\n .tasks-container {\n    display: flex;\n    flex-direction: column;\n    max-width: 1200px;\n    padding: 0 2em;\n    margin-bottom: 3em;\n    gap: 2em;\n  }\n\n  .container-title {\n    display: flex;\n    gap: 1em;\n  }\n\n  .container-title > h1 {\n    color: var(--medium-gray);\n    margin: 1em 0;\n  }\n\n  .upcoming-container > div + div,\n  .today-container > div + div,\n  .tasks-done > div + div{\n    display: flex;\n    flex-direction: column;\n    gap: 1em;\n  }\n\n  .card-container {\n    display: grid;\n    grid-template-rows: auto auto;\n    color: var(--para-color);\n    font-size: 1.5rem;\n    padding: var(--btn-padding);\n    border-radius: 10px;\n    box-shadow: 2px 2px 5px var(--shadow-color);\n  }\n\n  .task-card {\n    display: grid;\n    grid-template-columns: 45px 16em auto 2em 3em;\n    align-items: center;\n    gap: 1em;\n  }\n\n  .task-date {\n    display: flex;\n    gap: 1em;\n  }\n\n  .priority-div {\n    align-content: center;\n    justify-items: center;\n  }\n\n  .priority-ball {\n    height: 20px;\n    width: 20px;\n    border-radius: 50%;\n  }\n\n  .extension {\n    display: flex;\n    flex-direction: column;\n    max-height: 300px;\n    opacity: 1;\n    transition: 0.5s ease-in-out; \n  }\n\n  .extension-description {\n    padding: 1em 0;\n    display: flex;\n  }\n\n  .hide {\n    opacity: 0;\n    max-height: 0;\n    pointer-events: none; \n  }\n  \n  .extension-btns {\n    display: flex;\n    gap: 2em;\n    padding-bottom: 1em;\n  }\n\n  /* BUTTONS */\n\n  button {\n    border-style: none;\n    cursor: pointer;\n    padding: var(--btn-padding);\n    border-radius: 10px;\n  }\n\n  .add-project {\n    display: flex;\n    gap: 0.5em;\n    font-size: 1.25rem;\n    font-weight: 900;\n    align-items: center;\n    color: var(--dark-gray);\n    background-color: var(--light-gray);\n  }\n\n  .project-form > div > button,\n  .task-btns > button {\n    font-size: 1.2rem;\n    padding: var(--btn-padding);\n    border-radius: 10px;\n    color: var(--off-white);\n  }\n\n  .close-project,\n  .close-task,\n  .close-delete {\n    padding: 2px;\n    background-color: var(--sky-blue);\n    border-radius: 10x;\n  }\n\n  .submit-project,\n  .submit-task,\n  .confirm-delete,\n  .selection-btns > .cancel-delete,\n  .confirm-task,\n  .selection-btns > .cancel-task {\n    background-color: var(--sky-blue);\n    width: 100px;\n  }\n\n  .project-btn {\n    display: flex;\n    align-items: center;\n    gap: 1em;\n    font-size: 1.5rem;\n    font-weight: 900;\n    color: var(--dark-gray);\n    background-color: var(--light-gray);\n  }\n\n  .project-btn:hover {\n    text-decoration: underline;\n    text-decoration-thickness: 2px;\n    text-underline-offset: 4px;\n  }\n\n  .add-task,\n  .remove-project, \n  .edit-btn,\n  .remove-task {\n    display: flex;\n    gap: 0.5em;\n    font-size: 1.25rem;\n    font-weight: 900;\n    align-items: center;\n    color: var(--dark-gray);\n    background-color: var(--light-gray);\n  }\n\n  .project-btn,\n  .add-project,\n  .add-task,\n  .remove-project,\n  .edit-btn,\n  .remove-task {\n    transition: transform 0.3s ease-in-out;\n  }\n\n  .add-project:hover,\n  .project-btn:hover,\n  .add-task:hover,\n  .remove-project:hover,\n  .edit-btn:hover,\n  .remove-task:hover {\n    transform: scale(1.2);\n  }\n\n  .task-btns > button:hover,\n  .submit-project:hover,\n  .selection-btns > button:hover {\n    background-color: var(--hover-blue);\n  }\n\n  .selection-btns > button {\n    color: var(--off-white);\n  }\n\n  .extension-btn {\n    cursor: pointer;\n    justify-self: center;\n  }\n\n  .complete-btn {\n    padding: var(--btn-padding);\n    border: 2px solid var(--sky-blue);\n    border-radius: 0;\n    background-color: va;\n    font-size: 1rem;\n    font-size: 1.5rem;\n    font-weight: 900;\n    padding: 0;\n    width: 40px;\n    height: 40px;\n  }\n\n  .complete-btn:disabled {\n    border-color: #c0bfbf;\n    cursor: default;\n  }\n\n  /* MODALS */\n\n  dialog {\n    margin: 0 auto;\n    top: 20vh;\n    border-radius: 10px;\n    font-weight: 900;\n    color: var(--dark-gray);\n    border-style: none;\n    padding: 0;\n    box-shadow: 2px 2px 5px var(--medium-gray);\n  }\n\n  .modal-header {\n    display: flex;\n    justify-content: space-between;\n    background-color: var(--sky-blue);\n    color: var(--off-white);\n    padding: 0.25em 1em;\n  }\n\n  /* Project Modal */\n  \n  .project-form {\n    display: flex;\n    flex-direction: column;\n    gap: 1em;\n    padding: 1em;\n  }\n\n  .project-form > div {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5em;\n  }\n\n  label[for=name] {\n    font-size: 1.5rem;\n  }\n\n  input[type=text] {\n    font-size: 1.5rem;\n    padding-left: 0.2em;\n    color: var(--dark-gray);\n  }\n\n  ::backdrop {\n    background: var(--linear-gradient);\n    opacity: 0.7;\n  }\n\n\n  /* Task Modal */\n\n  .task-form {\n    padding: 1em 2em;\n  }\n\n  fieldset {\n    display: grid;\n    grid-template-rows: 1fr 2fr repeat(2, 1fr);\n    gap: 1em;\n    padding: 0;\n    margin: 1em 0;\n    border-style: none;\n  }\n\n  .description {\n    display: grid;\n    grid-template-rows: 1fr 4fr;\n  }\n\n  textarea {\n    resize: none;\n  }\n\n  fieldset > div,\n  .time > div {\n    display: grid;\n    grid-template-rows: 1fr 2fr;\n  }\n\n  .time {\n    display: flex;\n    gap: 1em;\n  }\n\n  /* Remove Modal */\n\n  .selection-btns {\n    display: flex;\n    gap: 0.5em;\n    padding: 1em;\n  }\n\n  .warning {\n    padding: 1em 1em 0 1em;\n    font-size: 1.5rem;\n  }"],"sourceRoot":""}]);
